
import { post, get } from './config'

export default{
    // 进入直播间    
    enterLiveRoom (data) {
        return post('api/live/into_live',data)
    },
    //离开直播间
    leaveLiveRoom (data) {
        return post('api/live/out_live',data)
    },
    // 获取直播信息
    getLiveTelecastInfo (data) {
        return post('api/Live/live_info',data)
    },
    // 获取用户信息
    getUserInfo(data){
        return post('api/user/user_info',data)
    }
    
    

}


