<template>
	<div class="liveRoom">
        <div class="liveRoom-top">
            <ul>
                <h2 class="allClass fl">全部分类</h2>
                <li v-for="item in classList">
                    {{item}}
                </li>
            </ul>
        </div>
        <div class="liveRoom-but">
            <div class="liveRoom-l">
                <!-- 直播标题 -->
                <div class="Room-tit">
                    <div class="Room-msg">
                        <div class="Room-uimg">
                            <img src="./imgs/素材14.png" alt="">
                        </div>

                        <div class="Room-gz"> 
                            <span class="Room-gz-sl">
                                    <i></i>关注    
                            </span>
                            <span class="Room-gz-sr">1234</span>
                        </div>
                    </div>
                    
                    <div class="Room-video-t">
                        <h3>直播标题</h3>
                        <p>直播间ID</p>
                        <h4>什么人</h4>
                    </div>
                    <div class="Room-share">
                        <span><i class="Room-Share-l" ></i>分享</span>
                        <span><i class="Room-Share-r" ></i>用手机看直播</span>
                    </div>
                </div>
                
                <!--  图片展示  -->
            <div class="Room-show">
                <img src="./imgs/素材14.png" alt="">
            </div>
                <!--  视频推荐  -->
                <div class="RoomVideo-groom">
                    <div class="RoomVideo-groom-txt">
                        <h3><i></i>视频推荐</h3>
                        <span></span>
                    </div>
                    
                    <video src="">

                    </video>
                    <video src="">

                    </video>
                    <video src="">

                    </video>
                </div>
            </div>
            <!-- 中间的视频区域 -->
            <div class="liveRoom-c">
                 <!-- 弹幕区  -->
                <div class="videoView" ref="videoView">

                </div>
               <video
                    id="my-player"
                    class="video-js vjs-default-skin"
                    controls
                    preload="auto"
                    poster="//vjs.zencdn.net/v/oceans.png"
                    >
                   
                <source src="//vjs.zencdn.net/v/oceans.m3u8" type="application/x-mpegURL">
                <!-- <source src="//vjs.zencdn.net/v/oceans.webm" type="video/webm">
                <source src="//vjs.zencdn.net/v/oceans.ogv" type="video/ogg">                 -->
            </video>
                <!-- http://live.streamingfast.net/osmflivech4.m3u8 -->
                                <!-- http://live.hkstv.hk.lxdns.com/live/hks/playlist.m3u8 -->
                <div class="liveInfo">
                    <!-- top -->
                    <div class="liveInfo-top">
                        <div class="liveInfo-img">
                            <img src="./imgs/素材3.png" alt="">
                        </div>
                        <div class="liveInfo-tle">
                            <h3>{{merchants}}<i></i></h3>
                            <p>{{anchorId}}<span><i></i>上海市浦东新区</span></p>
                        </div>
                        
                    </div>
                <p>个性签名：这个是文字哈士奇哈士奇哈士奇哈士奇哈士奇哈士奇哈士奇</p>
                <input type="button" value="TA的主页" class="liveInfo-btn">
                </div>
                <div class="boxImg">
                    <img src="./imgs/素材14.png" alt="">
                </div>
            </div>
        <!-- 右边的聊天室-->
            <div class="liveRoom-r">
              
                <!-- 直播时间 人数 礼物 -->
                <div class="RoomNum">
                    <div class="currentTime">
                        <p>12:20:23</p>
                    </div>
                    <ul>
                        <li class="RoomNum_left">31234</li>
                        <span class="RoomNum-fl"></span>
                        <li class="RoomNum_right">124124</li>
                    </ul>
                </div>
                <!--聊天室-->
                    <div class="ChatRoom" ref="ChatRoom">
                        <!--正常聊天消息-->
                        <div class="ChatNews ">
                            <p><span class="GetChat-use"><em>用户名 </em></span><span>信息聊天天d聊天信息聊天信息</span></p>
                        </div>
                        <!--进入聊天室消息-->
                        <!-- <div class="GetChat">
                            <span class=""><span class="GetChat-use">风青杨 </span>驾临直播间</span>
                        </div> -->
                        <!--礼物通知消息-->
                        <div class="GiftNews">
                            <span class="UserName">杨过  </span>
                            <span>赠送给</span>
                            <span class="ZB-name">主播名</span>
                            <span class="GiftImg">
                                <img src="./imgs/aixin.png" alt="">
                            </span>
                            <span class="GiftNums">X 2</span>
                        </div>
                    </div>
                        <!-- 消息框 -->
                <div class="RoomMeg-box">
                    <div class="RoomShadow">
                        <img src="./imgs/danm_off.png" alt="" v-if="ok" @click="DMSwitch">
                        <img src="./imgs/danm_on.png" alt="" v-else @click="DMSwitch">
                        <!-- <img src="./imgs/danm_on.png" ref="DanMuImg"/> <input type="button" value="开启弹幕" ref="DanMu" @click="DMSwitch"> -->
                    </div>

                    <div class="RoomMsg-inp">
                        <input type="text" class="Msg-txt" ref="inp"><input class="Msg-inp" type="button" value="点击发送" @click="sendRoomText">
                    </div>
                </div>
            </div>
        </div>  
    </div>
</template>


<script>
import api from '@/api/live' 
import Vue from 'vue' 
//  随机函数
// Vue.prototype.random = function(n,m){
//   return parseInt(Math.random()*(m - n + 1) + n);
// }

    export default{
        data(){
            return{
                classList:["全部","服装双打","健康","分类","分类","分类","分类"],
                videoSrc:'http://live.hkstv.hk.lxdns.com/live/hks/playlist.m3u8',//直播地址
                liveRoomInfo:'',    //..直播间消息
                isFollow:"1",  //关注
                anchorId:'',    //用户消息
                merchants:'',
                ok:'false',
                DanMuState:'', //1开启弹幕  0 关闭弹幕
            }
        },
        methods:{
            
           
            // 判断弹幕按钮 
            DMSwitch(){
                this.ok  = !this.ok;
                if(this.ok){
                    this.DanMuState = 0
                    console.log("开关的状态是" + this.DanMuState)
                }else{
                    this.DanMuState = 1
                    console.log("开关的状态是" + this.DanMuState)
                    
                }
            },
            
            joinRoom() {
                // 加入聊天室
                this.conn.joinChatRoom({
                    roomId: this.liveRoomInfo.room_id // 聊天室id
                });
            },
            quitRoom() {
                // 退出聊天室
                this.conn.quitChatRoom({
                    roomId: this.liveRoomInfo.room_id // 聊天室id
                });
            },
             /*添加消息到聊天室方法*/
            //   注意 这里的环信测试使用的是固定的账号 没有改过来 在login页面
        sendRoomText() {
            // 不能发送空消息
            if(!this.$refs.inp.value){
                return false;
            }
            // 判断弹幕是否开启 1是开启
            if(this.DanMuState == 1){
                // 随机函数
                function randoms(n,m){
                    return parseInt(Math.random()*(m - n + 1) + n);
                }

                console.log(this.DanMuState);
                // 弹幕方法 
                function DanMuRun(){
                    // 创建弹幕 并插入到遮罩层
                    var videoView = document.getElementsByClassName("videoView")[0]
                    var inpValue = document.getElementsByClassName("Msg-txt")[0].value
                    var ps = document.createElement("p");
                    ps.innerHTML = inpValue;
                    ps.setAttribute("class","danmutxt");
                    ps.style.top = randoms(0,180)+"px"; //弹幕出现 随机位置
                    videoView.appendChild(ps);
                    var times = setInterval(function(){
                        var speed = 1;
                        ps.style.right = 0 + speed + "px";
                    },100)
                    
                }
                DanMuRun()
            }
            // 创建消息插入到聊天室
            var MsgCom = Vue.extend({
                    template:`<div class="ChatNews ">
                            <p><span class="GetChat-use"><em>${this.userInfo.username} </em></span><span>${this.$refs.inp.value}</span></p>
                        </div>`
                })
            var msgCom = new MsgCom().$mount();
            //  清空输入框
            this.$refs.inp.value = '';
            var ChatRoom = this.$refs.ChatRoom;
            // console.log(ChatRoom);
            var id = this.conn.getUniqueId();         // 生成本地消息id
            var msg = new WebIM.message('txt', id); // 创建文本消息
            var option = {
                msg: this.$refs.inp.value,          // 消息内容
                
                to: this.liveRoomInfo.room_id,               // 接收消息对象(聊天室id)
                roomType: true,
                chatType: 'chatRoom',
                success: function () {
                    
                    console.log("消息发送成功！！")
                    
                    ChatRoom.appendChild(msgCom.$el);

                },
                fail: function () {
                    console.log('failed');
                }
            };
            msg.set(option);
            msg.setGroup('groupchat');
            this.conn.send(msg.body);
        },
      
            
        },
        
        created(){
            api.getUserInfo({
                uid: this.getCookie('uid'),
                token: this.getCookie('token'),
            }).then(
                res =>{
                    console.log(res.data)
                    this.userInfo = res.data;
                     console.log(this.userInfo.hx_username,this.userInfo.hx_password)
                     //  环信登录
                     this.conn.open({
                        apiUrl: WebIM.config.apiURL,
                        user: 'gaodam',//this.userInfo.hx_username,
                        pwd: 'cszzlwl199114',//this.userInfo.hx_password,
                        
                        appKey: WebIM.config.appkey,
                        success:function(){
                            console.log('登录成功！');
                        },
                        error: function(){
                        
                            console.log("登录失败！")
                        }
                    })
                     
                    
                }
            );
            //获取直播间消息
            api.getLiveTelecastInfo({
                // http://shop.100ytv.com/mall_live/#/liveRoom_mobile?live_id=50&room_id=48713890332673
                live_id: 64  //this.getLiveId("live_id"),
                
            }).then(//数据请求
                (res) => {
                    if(res.status === 'ok'){
                        this.liveRoomInfo = res.data;
                        this.anchorId= "ID号：" + res.data.room_id
                        this.merchants = res.data.username
                        this.videoSrc = res.data.play_address_m3u8; //  直播地址
                        var myPlayer = videojs("my-player");//动态改变viddeo的视频路径
                            myPlayer.src(this.videoSrc);
                            myPlayer.load(this.videoSrc)
                    }else if(res.status === 'error'){
                        console.log(res.data);
                    }
                }),
            // 获取用户信息
            
            this.conn.listen({
                /*连接成功回调*/
                    onOpened:function(message){
                        console.log("连接成功")
                        // console.log(message);
                        // 如果config.js里面isAutoLogin设置为false，那么必须手动设置上线，否则无法收消息
                        $scope.conn.setPresence();//手动上线
                    },
                    /*连接失败回调*/
                    onClosed:function(message){
                        console.log("连接失败");
                    },
                    onTextMessage:function(message){ 
                        // 聊天室
                        // if(message.type=='chatroom' && message.to==this.liveRoomInfo.room_id){ 
                        //     console.log(message);
                        //     console.log("聊天室收到消息");
                        //     var chatBoxs = document.getElementsByClassName("ChatRoom")[0];
                        //     chatBoxs.appendChild('<div class="ChatNews "><span class="s_chatRoom_name col_63a2d6"><em>' + message.ext.username + '</em>：</span><span class="s_chatRoom_infor">' + message.data + '</span></div>');
                           
                        // }
                    }, /*接受命令消息*/
                    onCmdMessage: function (message) {
                        console.log(message.action);
                        if(message.action == 'anchorStopLive'){
                            console.log('主播已下线');
                        }
                    },
                    /*聊天室相关回调*/
                    onPresence: function (message) {
                        this.sendRoomText(message);
                        
                    },
                    // 收到消息送达服务器回执
                    onReceivedMessage(msg){
                        console.log(msg);
                    },
                    /*本机网络掉线*/
                    onOffline: function () {
                        //建议写一个断线重连的处理
                    },
                    /*失败回调*/
                    onError : function(message){
                        console.log(message)
                    }
            })
            // 用户进入直播间 获取关注信息
            api.enterLiveRoom({
                live_id:64,//this.getLiveId("live_id"),
                uid: this.getCookie('uid'),
                token: this.getCookie('token'),
            }).then(
                (res) => {
                    if(res.status === 'ok'){
                        console.log(res.data);
                        
                        this.isFollow = res.data.is_follow;
                         var UserEnter = Vue.extend({
                                    template:`<div class="GetChat">
                                                <span class=""><span class="GetChat-use">${this.getCookie('userInfo').username} </span>驾临直播间</span>
                                            </div>`
                                })
                        var userEnter = new UserEnter().$mount();
                        this.$refs.ChatRoom.appendChild(userEnter.$el);
                        }
                    }
                )
            
            

        },
        mounted(){}
    }
</script>

<style>
  
    /*  改变按钮的默认样式 */
.video-js .vjs-big-play-button {
    height:45px;
    width: 45px;
    border-radius: 50%;
    border:1px solid #fff;
    position: absolute;
    top:50%;
    left:50%;
    margin: -22px 0 0 -22px;
    z-index: 12312;
}
/** 直播页导航栏 **/
.liveRoom-top{
    height: 40px;
    width: 1000px;
    margin: 20px 0 0 40px;
}
.liveRoom-top ul li{
    float: left;
    height:30px;
    line-height: 30px;
    text-align: center;
    font-size: 16px;
    margin-left:12px;
    border:1px solid #ccc;
    padding:0 12px;
    border-radius: 15px;
    font-size: 12px;
    font-weight: 900;
    cursor: pointer;
    background: #fff;
}
/**     聊天室      **/
.ChatNews{
    margin: 5px 0;
}
.GiftNews{
    min-width:375px;
    min-height:30px;
    line-height: 30px;
    margin: 5px 0;    
}
.ZB-name{
    color:chartreuse;
}
.UserName{
    color:#FFC125;
    color: 18px;
}
.GiftImg{
    margin-right:10px;
}
/*   礼物图片   */ 
.GiftNews .GiftImg{
    max-width: 40px;
    max-height: 60px;
    display: inline-block;
}
.GetChat{
    min-height:30px;
    background: #EED5B7;
    line-height: 30px;
    margin: 5px 0;
}
.GetChat-use{
    color:cyan;
    font-size: 16px;
}
.ChatRoom{
    width:380px;
    height:480px;
    margin-top:10px;
    border:1px solid #ccc;
    float: left;
    text-indent: 8px;
    background: #fff;
    border-radius: 5px;
}
/**     主播信息    **/ 
.liveInfo-tle{
    width:310px;
    height:60px;
    float: left;
    margin: 20px 0 0 24px; 
}
.boxImg{
    width:580px;
    height:360px;
    margin-top:10px;
}
.boxImg img{
    width:100%;
    height: 100%;
}
.liveInfo-btn{
    height: 32px;
    width:180px;
    background: #458B00;
    margin:12px 0 0 190px;
    border-radius: 16px;    
    color: #fff;
    text-align: center;
    line-height: 36px;
}
.liveInfo-tle i{
    display: inline-block;
    width: 18px;
    height: 18px;
    background: url("./imgs/aixin.png") no-repeat center bottom;
    margin-left: 8px;
}
.liveInfo-top{
    overflow: hidden;
}
.liveInfo{
    height:160px;
    width: 580px;
    background: #fff;
    border-radius: 5px;
    border: 1px solid #ccc;
}
.liveInfo-img{
    height:60px;
    width:60px;
    float: left;
    margin:5px 0 0 8px;
}
.liveInfo img{
    width: 100%;
    height: 100%;
}
/** 模版的根元素 **/ 
.liveRoom{
    overflow: hidden;
    width:1280px;
    margin: 0 auto;
}
/** 聊天消息框 **/
.RoomMeg-box{
    width:380px;
    height:110px;
    margin-top:10px;
    background: #fff;
    border:1px solid #ccc;
    border-radius: 5px; 
    float: left;   
} 
.RoomShadow{
    height:40px;
    width:120px;
    line-height: 40px;
    text-align: center;
    float: right;
}
.RoomShadow img{
    width: 40px;
    height:20px;
    /* position: relative;
    top:7px; */
}
.RoomShadow input{
    width:60px;
    height:30px;
}
.RoomMsg-inp{
    width:380px;
    height:50px;
    float: left;
    
}
.Msg-txt{
    width:274px;
    height: 40px;
    border:2px solid #ccc;
    border-right:none;
    float: left;
    margin-left:5px;
}
.Msg-inp{
    width: 90px;
    float: left;
    height:40px;
    background: #458B00;
}
/**     视频推荐     **/
.RoomVideo-groom{
    width:260px;
    float: left;
}
.RoomVideo-groom-txt{
    border-bottom:1px solid #ccc;
    padding-bottom: 20px;
    position: relative;
}
.RoomVideo-groom-txt i{
    display:inline-block;
    height:24px;
    width: 28px;
    background: url("./imgs/sptj.png") no-repeat 100%;
    position: relative;
    top:6px;
}
.RoomVideo-groom span{
    width: 40px;
    height:4px;
    display: inline-block;
    background: #458B00;
    position: absolute;
    bottom: 0px;
}
.RoomVideo-groom video{
    margin-top:20px;
}
/**     左中右布局     **/
.liveRoom-l{
    width: 282px;
    min-height: 600px;
    overflow: hidden;    
    float: left;
    padding-left: 18px;
}
.liveRoom-c{
    float: left;
    min-height: 900px;
    overflow: hidden;
    margin-left:10px;
    position: relative;
}
.liveRoom-r{
    float: left;
    min-height:900px;
    width:380px;
    margin:12px 0 0 10px;
}
/**      video             **/ 
#my-player{
    width:580px;
    height:360px;
    margin: 12px 0 12px 0;
}
 /* 弹幕 */
.videoView{
    position: absolute;
    width: 580px;
    height:220px;
    margin-top: 10px;
    z-index: 120;
    left:0;
    background: #458B00;
    opacity: 0.4;
}
.danmutxt{
    margin:1px 1px 1px 1px;
    padding: 0 10px;
    color:#fff;
    height:30px;
    line-height: 30px;
    border:1px solid red;
    border-radius: 30px;
    display: inline-block;
    position: absolute;
    right:0px;
    font-size: 18px;
}
/**     图片展示      **/
.Room-show{
    width:260px;
    height:150px;
    margin: 10px 0 0 0;
    float: left;
}
.Room-show img{
    width: 100%;
    height: 100%;
}

/**       直播人数          **/
.RoomNum{
    height:120px;
    width:380px;
    border:1px solid #ccc;
    background: #fff;
    padding:0px 6px;
    float: left;
    border-radius: 5px;  
}
.currentTime{
    text-align: center;
    height:59px;
    line-height: 59px;
    border-bottom:1px solid #ccc;
    font-weight: 900;
    font-size: 18px;
}
.RoomNum li{
    text-align: center;
    width:169px;
    line-height: 60px;
    float: left;
}
.RoomNum-fl{
    float: left;
    width:2px;
	background: #000;
	height:24px;
	margin-top:18px;
}
.RoomNum_left{
    background: url("./imgs/zxs.png") no-repeat 24px center;
    background-size: 16px;
}
.RoomNum_right{
    background: url("./imgs/lw.png") no-repeat 24px center ;
    background-size: 16px;
}
/**       直播标题          **/ 
    .Room-tit{
        margin-top: 12px;
        float: left;
        width:260px;
        height:220px;
        border:1px solid #ccc;
        background: #fff;
        border-radius: 5px;
        
    }
    .Room-uimg{
        width: 120px;
        height: 120px;
        float:left;
    }
    .Room-uimg img{
        height:100%;
        width: 100%;
    }
    .Room-msg{
        height:111px;
        overflow: hidden;
    }
    .Room-gz{
        float:right;
        width:120px;
        height:24px;
        line-height: 24px;
        background: #fff;
        border-radius: 12px;
        margin: 10px 5px;
    }
    .Room-gz-sl{
        float: left;
        height:24px;
        width:60px;
        border-top-left-radius:12px;
        border-bottom-left-radius:12px;  
        background: #458B00;
        font-weight: 800;
    }
   .Room-gz-sl i{
       display: inline-block;
       height:18px;
       width: 18px;
       background:url("./imgs/aixin.png") no-repeat 100%;
       position: relative;
       top:3px;
       margin-right: 4px;
   }
   .Room-gz-sr{
       float: left;
       width:60px;
       height:24px;
       text-align: center;
       color:#ccc;
       border: 1px solid #ccc;
       border-left:none;
       border-top-right-radius:12px;
       border-bottom-right-radius:12px;  
   }
   .Room-video-t{
       margin-top:8px;
   }
   .Room-share i{
       display: inline-block;
       height:18px;
       width: 18px;
       position: relative;
       top:6px;
   }
   .Room-share{
       float: right;
       font-size: 12px;
       margin-right: 8px;
   }
   .Room-share span{
       margin-left: 18px;
   }
   .Room-Share-l{
       background:url("./imgs/fxzb.png") no-repeat;    
   }
   .Room-Share-r{
       background:url("./imgs/sjzb.png") no-repeat;    
   }
</style>


